import React, { useEffect, createElement, useState, useRef } from 'react'

const ParallaxItem = ({ move, children }) => {
    const [ item, setItem ] = useState(null)
    const ref = useRef()

    const parallax = (e) => {
        let x = (e.clientX / window.innerWidth) 
        let y = (e.clientY / window.innerWidth) 

        let transformCode = ''

        if (move.left) {
            transformCode = `-${x * move.left}${move.type}, `
        }

        if (move.right) {
            transformCode = `+${x * move.right}${move.type}, `
        }

        if (move.top) {
            transformCode = transformCode + `-${y * move.top}${move.type}`
        }

        if (move.bottom) {
            transformCode = transformCode + `+${y * move.bottom}${move.type}`
        }

        ref.current.style.transform = `translate(${transformCode})`
    }

    useEffect(() => {
        setItem(createElement(children.type, {...children.props, ref: ref}))
    }, [])

    useEffect(() => {
        if (item) document.addEventListener('mousemove', parallax)
    }, [item])
    
    return item
}

export default ParallaxItem